<template>
    <div>
        <header class="banner
         position-relative"> <!-- Banner -->
            <ul class="list-unstyled my-slider mb-0">
                <li><div class="w-100 carousel-img" style="background: url('./assets/images/index_banner_carousel_3.jpg') 20% center/cover;"></div></li>
                <li><div class="w-100 carousel-img" style="background: url('./assets/images/index_banner_carousel_1.jpg') 20% center/cover;"></div></li>
                <li><div class="w-100 carousel-img" style="background: url('./assets/images/index_banner_carousel_2.jpg') 20% center/cover;"></div></li>
            </ul>
            <div class="container banner-cover position-absolute d-flex align-items-center">
                <div class="banner-slogan p-3 pr-5 text-white">
                    <h2 class="display-4"><br></h2>
                    <p class="h3 mt-3"></p>
                </div>
            </div>
        </header>
        <section class="section section-copywriting"> <!-- copywriting -->
            <div class="container">
                <h3 class="section-title"></h3>
                <ul class="list-unstyled row features w-90">
                    <li class="col-md-4">
                        <div class="mb-2"><font-awesome-icon :icon="['fas','fish']" size="5x" :style="{'color': '#FFA500'}" /></div>
                        <p></p>
                    </li>
                    <li class="col-md-4">
                        <div class="mb-2"><font-awesome-icon :icon="['fas','couch']" size="5x" :style="{'color': '#FFA500'}" /></div>
                        <p></p>
                    </li>
                    <li class="col-md-4">
                        <div class="mb-2"><font-awesome-icon :icon="['fas','home']" size="5x" :style="{'color': '#FFA500'}" /></div>
                        <p></p>
                    </li>
                </ul>
            </div>
        </section>
        <section class="section section-new-items">
            <div class="container">
                <div class="mx-auto">
                    <h4></h4>
                    <nav class="position-relative">
                      <div class="slide-control">
                        <button class="slide-left"><font-awesome-icon :icon="['fas','chevron-left']" size="3x" /></button>
                        <button class="slide-right"><font-awesome-icon :icon="['fas','chevron-right']" size="3x" /></button>
                      </div>
                      <ul v-if="recomProd.length" class="list-unstyled new-item-list d-flex">
                        <li v-for="prod in recomProd" :key="prod.id">
                            <div class="card position-relative border border-primary h-100">
                              <img :src="prod.imageUrl" class="card-img-top w-75 mx-auto" alt="">
                              <div class="icons">
                                <button class="btn p-0 icon-btn" @click="addToCart(prod.id)"><font-awesome-icon class="d-block" icon="cart-plus" size="2x" /></button>
                              </div>
                              <div class="card-body px-3 pb-3 pt-1 d-flex flex-column justify-content-between">
                                <div>
                                  <div class="product-brand">{{prod.title.brand}}</div>
                                  <div class="product-collection mb-0">{{prod.title.collection}}</div>
                                  <div class="product-type text-muted">{{prod.title.type}}</div>
                                </div>
                                <em class="product-price">${{prod.price}}</em>
                              </div>
                            </div>
                        </li>
                      </ul>
                    </nav>
                </div>
            </div>
        </section>
        <section class="section section-recommand text-white"> <!-- Recommandation from customers -->
            <div class="container">
                <h3 class="section-title"></h3>
                <ul class="row list-unstyled">
                    <li class="col-md-4">
                        <blockquote class="blockquote">
                            <p></p>
                            <footer class="blockquote-footer text-right">Some customer</footer>
                        </blockquote>
                    </li>
                    <li class="col-md-4">
                        <blockquote class="blockquote">
                            <p></p>
                            <footer class="blockquote-footer text-right">Some customer</footer>
                        </blockquote>
                    </li>
                    <li class="col-md-4">
                        <blockquote class="blockquote">
                            <p></p>
                            <footer class="blockquote-footer text-right">Some customer</footer>
                        </blockquote>
                    </li>
                </ul>
            </div>
        </section>
        <section class="coupon-evt bg-light">
          <div class="container coupon-evt__info">
            <div class="row">
              <div class="col-md-6">
                <img class="img-fluid" src="~@/assets/images/coupon_sideImg.jpg" alt="">
              </div>
              <div class="col-md-6">
                <div class="h-100 py-4 d-flex flex-column justify-content-center text-center">
                  <div class="mb-4 align-self-center coupon-evt__info--title"></div>
                  <p>
                     <strong>MayCatsBeWithYou</strong></p>
                  <router-link to="/products/treats/all" class="btn btn-primary btn-lg align-self-center"></router-link>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
</template>

<script>
import { tns } from 'tiny-slider/src/tiny-slider'
import 'tiny-slider/dist/tiny-slider.css'
import { mapState } from 'vuex'

export default {
  data () {
    return {
      recomProd: []
    }
  },
  methods: {
    randomRecom (prodArr) {
      if (prodArr.length > 0) {
        for (let i = 0; i < 8; i++) {
          const selInd = Math.floor(Math.random() * prodArr.length)
          this.recomProd.push(prodArr.splice(selInd, 1)[0])
        }
        this.$nextTick(function () {
          tns({
            container: '.new-item-list',
            gutter: 15,
            controlsContainer: '.slide-control',
            navPosition: 'bottom',
            responsive: {
              768: {
                items: 3
              },
              992: {
                items: 4
              }
            }
          })
        })
      }
    },
    addToCart (productId, qty = 1) {
      const vm = this
      this.$store.dispatch('await', true)
      this.$store.dispatch('addCartItem', { productId, qty }).then(() => {
        vm.$store.dispatch('await', false)
      })
    }
  },
  computed: mapState(['products']),
  watch: {
    products () {
      const arr = [...this.products]
      this.randomRecom(arr)
      this.$store.dispatch('await', false)
    }
  },
  mounted () {
    this.$store.dispatch('await', true)
    if (this.products.length) {
      const arr = [...this.products]
      this.randomRecom(arr)
      this.$store.dispatch('await', false)
    }
    tns({
      container: '.my-slider',
      mode: 'gallery',
      autoplay: true,
      speed: 1000,
      controls: false,
      nav: false,
      autoplayButtonOutput: false
    })
  }
}
</script>
